<template>
    <div>

        <h1>전화번호부</h1>

        <h2>리스트</h2>

        <p>등록된 전화번호 리스트입니다.</p>

        <table border="1">
            <colgroup>
                <col style="width: 100px;">
                <col style="width: 120px;">
                <col style="width: 120px;">
                <col style="width: 190px;">
            </colgroup>
            <thead>
                <tr>
                    <th>이름(name)</th>
                    <th>핸드폰(hp)</th>
                    <th>회사(company)</th>
                    <th>관리</th>
                </tr>
            </thead>
            <tbody>
                <tr v-bind:key="i" v-for="(personVo, i) in personList">
                    <td>{{ personVo.name }}</td>
                    <td>{{ personVo.hp }}</td>
                    <td>{{ personVo.company }}</td>
                    <td>
                        <button v-on:click="removePserson(personVo.personId, i)" type="button">삭제하기</button>&nbsp;&nbsp;
                        <router-link  v-bind:to="`/modify/${personVo.personId}`">[수정폼 이동]</router-link>
                    </td>
                </tr>
            </tbody>
        </table>
        <br>

        <router-link to="/write">등록폼 이동</router-link>

    </div>
</template>


<script>
import axios from 'axios';

export default {
    name: "ListView",
    components: {},
    data() {
        return {
            personList: []
        };
    },
    methods: {
        getList() {
            console.log("getList()");
            console.log(this.$store.state.apiBaseUrl);
            axios({
                method: 'get',  //put,post,delete
                //url: 'http://localhost:9000/api/persons',
                url: `${this.$store.state.apiBaseUrl}/api/persons`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //전송타입
                //params: guestbookVo, //get방식 파라미터로 값이 전달
                //data: this.guestbookVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data); //수신데이타
                this.personList = response.data.apiData;

            }).catch(error => {
                console.log(error);
            });
        },
        removePserson(personId, index) {
            console.log(personId);
            console.log("removePserson()");
            console.log(this.$store.state.apiBaseUrl);
            axios({
                method: 'delete',  //put,post,delete
                url: `${this.$store.state.apiBaseUrl}/api/persons/${personId}`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //전송타입
                //params: guestbookVo, //get방식 파라미터로 값이 전달
                //data: this.guestbookVo, //put, post, delete 방식 자동으로 JSON으로 변환 전달

                responseType: 'json' //수신타입
            }).then(response => {
                console.log(response.data.result); //수신데이타

                if (response.data.result == "success") {
                    this.personList.splice(index, 1);
                    
                }else {
                    alert("삭제되지 않았습니다.");
                }

            }).catch(error => {
                console.log(error);
            });
        }

    },
    created() {
        this.getList();
    }
};
</script>


<style></style>